<template>
  <transition name="appear">
    <div class="modal">
      <h1>
        {{ letter }}
      </h1>
      <img src="@/assets/close.png" width="30px" class="close" @click="$emit('close')" />
      <p>{{ msg }}</p>
    </div>
  </transition>
</template>

<script>
export default {
  name: "Hint",
  props: {
    letter: String,
    msg: String,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.modal {
  background-color: #1f1f1f;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 300;
  padding: 40px;
  width: 90%;
  height: 80%;
  color: white;
  box-shadow: 0px 0px 200px black;
}

.appear-enter-active,
.appear-leave-active {
  transition: 0.5s;
}
.appear-enter, .appear-leave-to /* .fade-leave-active below version 2.1.8 */ {
  top: 200%;
}
h1 {
  color: #000000;
  float: left;
  margin-top: 0px;
  background-color: #ffffff;
  border: 5px solid #ffffff;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-bottom-right-radius: 20px;
  position: absolute;
  top: 0px;
  left: 0px;
}
.close {
