@function random_range($min, $max) {
  $rand: random();
  $random_range: $min + floor($rand * (($max - $min) + 1));
  @return $random_range;
}

.snow {
  color: white;
  font-size: 10vw;
  $total: 200;
  position: absolute;
  z-index: 100;
  width: 10px;
